<template>
  <def-layout>
    <h3 slot="layout_title">新增报价单</h3>

    <div slot="layout_inner_head">

      <h5 class="fw-none mt-2">
        选择菜品
      </h5>

      <table class="table m-0">
        <tr class="text-left">
          <td width="10%">序号</td>
          <td width="15%">货号</td>
          <td width="30%">描述</td>
          <td width="25%">单价</td>
          <td width="10%"></td>
          <td width="10%" class="text-center">选择操作</td>
        </tr>
      </table>
    </div>

    <prices-plus-body slot="layout_inner_body"
      :selected="select"
      :prices_contented="price_content"
      @removeItemChild="removeItem"
      @addItemChild="addItem"
      v-if="price_content.length > 0"
    >
    </prices-plus-body>

    <div slot="layout_footer">
      <div class="row">
        <div class="col-sm-3">

          <h5 class="fw-none text-accent">其他信息</h5>
          <listing-member-face-card :the_member="member" v-if="member!=null"></listing-member-face-card>

        </div>
        <div class="col-sm-3 pt-4">
          
          <div class="fs-14 timed-wrapper">
            <simple-input 
              @returnValue="reciverTimed" 
              :the_tip_header="`报价单号`"
              :the_placeholder="`- -`"
              :the_min_length="3"
              :the_max_length="12"
              >
              <div slot="input_txt">报价时间：</div>
            </simple-input>
          </div>
          <div class="fs-14 timed-wrapper">
            <simple-input 
              @returnValue="reciverTimed" 
              :the_tip_header="`报价时间`"
              :the_placeholder="`yyyy-mm-dd`">
              <div slot="input_txt">报价时间：</div>
            </simple-input>
          </div>

        </div>
        <div class="col-sm-3 flex-center">
          
        </div>
        <div class="col-sm-1 flex-center">
          
        </div>
        <div class="col-sm-2 pt-2">
          
          <prices-plus-footer @pricesPdfView_Father="pricesPdfView"></prices-plus-footer>

        </div>
      </div>
    </div>

    <div slot="layout_extra">
      <member-choise-modal @returnChoised="reciverChoised"></member-choise-modal>
      <prices-pdf-modal 
        :the_member="member" 
        :the_prices_timed="timed_prices" 
        :the_prices_numed="numed_prices"
        :the_prices_content="select_content"
      >
      </prices-pdf-modal>
    </div>

  </def-layout>
</template>

<script>
import $ from 'jquery'
import service from '@/service'
import view from '@/common/es/view'
import tool from '@/common/es/tool'

import DefLayout from '@/components/Layout/DefLayout.vue'
import PricesPlusBody from './Body/PricesPlusBody.vue'

import conn from '@/service/conn'
import MemberChoiseModal from '@/components/Modal/Choise/MemberChoiseModal.vue'
import ListingMemberFaceCard from '@/components/Card/ListingMemberFaceCard.vue'

import PricesPlusFooter from './Footer/PricesPlusFooter.vue'
import SimpleInput from '@/components/Input/Simple/SimpleInput.vue'
import PricesPdfModal from '@/components/Modal/Pdf/PricesPdfModal.vue'

export default {
  components: { DefLayout, PricesPlusBody, MemberChoiseModal, ListingMemberFaceCard, SimpleInput, PricesPlusFooter, PricesPdfModal },
  name: 'PricesPlus',
  data() {
    return {
      price_content: [],
      select: [], // id
      select_content: [],

      member: null,
      numed_prices: '0000',
      timed_prices: ''
    }
  },
  created() {

  },
  mounted() {
    // 验证 客户信息
    if (this.member == null) {
      tool.showModal('memberChoiseModal')
    }

    // 加载 价格内容
    conn.freight.getList_Freight().then(res => {

      this.price_content = []
      for (const f of res) {
        
        this.price_content.push(
          {
            id: f.id,
            num: f.num,
            named: f.named,
            unit: f.unit,
            price: f.price,
          }
        )
      }
    })
    
  },
  methods: {
    reciverChoised(v) {
      this.member = v
    },
    reciverTimed(v) {
      this.timed_prices = v
    },
    reciverNumed(v) {
      this.numed_prices = v
    },
    // 
    pricesPdfView() {
      this.select_content = this.price_content.filter((e) => {
        if (tool.inList(e.id, this.select)) {
          return true
        }
        return false
      })

      tool.showModal('pricesPdfViewModal')
    },
    //
    removeItem(id) {
      const i = tool.indexList(id, this.select)
      if (this.select.length < 2) {
        tool.showToast('至少选择一项菜品。')
      } else {
        this.select.splice(i, 1)
      }
    },
    addItem(id) {
      const i = tool.indexList(id, this.select)
      if (i < 0) {
        this.select.push(id)
      }
    },
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/content.sass"

.timed-wrapper
  display: inline-block
  line-height: 1
  height: 24px
  // float: right
  // position: absolute
  // bottom: $distance

</style>


